import React, { useEffect, useState } from "react";
import sanityClient from "../client.js";
import image from "../bg.jpg";
import imageUrlBuilder from "@sanity/image-url";
import BlockContent from "@sanity/block-content-to-react";

const builder = imageUrlBuilder(sanityClient);
function urlFor(source) {
	return builder.image(source);
}

export default function About() {
	const [author, setAuthor] = useState(null);

	useEffect(() => {
		sanityClient
			.fetch(
				`*[_type == "author"]{
			name,
			bio,
			"authorImage": image.asset->url
		}`
			)
			.then((data) => setAuthor(data[0]))
			.catch(console.error);
	}, []);

	if (!author) return <div>Loading...</div>;

	return (
		<main className="relative ">
			<img
				src={image}
				alt="Swedish winter"
				className="absolute min-w-screen min-h-screen object-cover overflow-hidden lg:h-full 2xl:w-full"
			/>
			<div className="p-10 lg:pt-48 container mx-auto relative">
				<section className="bg-gray-600 rounded-lg shadow-2xl lg:flex p-5 md:p-10 lg:p-15">
					<img
						src={urlFor(author.authorImage).url()}
						alt={author.name}
						className="rounded w-auto h-16 md:h-32 lg:w-auto lg:h-64 mr-8"
					/>
					<div className="text-lg flex flex-col justify-center">
						<h1 className="cursive text-1xl md:text-4xl lg:text-6xl text-green-300 mb-4">
							Hi there! I'm <span className="text-green-100">{author.name}</span>
						</h1>
						<div className="prose prose-sm md:prose-md lg:prose-xl text-white">
							<BlockContent blocks={author.bio} projectId="gj7javeh" dataset="production" />
						</div>
					</div>
				</section>
			</div>
		</main>
	);
}
